<template >

  <transition name="showRouter"><!-- vue.js过渡效果 -->
    <div class="music-list">

      <div v-for="(item, index) of musicData" class="music-item">
        <img v-bind:src="item.musicImgSrc || 'https://microzz.com/img/avatar.jpg'" class="music-img"></img>
        <span @click="toggleMusic(index)" class="music-name">{{ (index+1) + '.&nbsp; ' + item.name}}</span>
        <span v-on:click="del(index)" class="del-icon"></span>
      </div>

      <div class="tips">我是有底线的</div><!-- 底层显示 -->

    </div>
  </transition>

</template>

<script>

export default {
  name: 'MusicList',
  beforeCreate() {
    this.$store.commit('showMiniMusic', true);
  },
  mounted() {
    this.$store.commit('changeLinkBorderIndex', 1);
  },
  methods: {
    //点击切换音乐
     toggleMusic(index) {
      if (this.$store.state.audio.index === index) {
        this.DOM.audio.paused ? this.DOM.audio.play() : this.DOM.audio.pause();
        this.$store.commit('play', !this.isPlaying);
      } else {
        this.DOM.audio.play();
        this.$store.commit('play', true);
      }
      this.$store.commit('toggleMusic', index);

    },
  
    // 删除音乐
    del(index) {
      console.log("点击删除，准备去到mutation")
      this.$store.commit('del', index);
    }
  },
  computed: {
    musicData() {
      return this.$store.state.musicData;
    },
    DOM() {
      return this.$store.state.DOM;
    },
    isPlaying() {
      return this.$store.state.isPlaying;
    }
  },
}
</script>

<style >
  .showRouter-enter-active {
    transition: all .4s ease;
  }
  .showRouter-leave-active {
    transition: all 0 ease;
  }
  .showRouter-enter, .showRouter-leave-active {
    transform: translateX(-7.5rem);
    opacity: 0;
  }
  .music-list {
    padding-top: 4.15rem;
    padding-left: 0.2rem;
    padding-right: 0.2rem;
    margin-bottom: 3.5rem;
    flex:8;
    overflow: auto;
  }
  .music-item + .music-item {
    border-top: 0.05rem solid rgba(0, 0, 0, .1);
  }
  .music-item {
   
    padding: 0.2rem 0.3rem 0 0.3rem;
    position: relative;
    margin-bottom: 0.2rem;
    border-radius: 0.25rem;
    cursor: pointer;
    border: none;
  }
  .music-img {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 0.25rem;
  }
  span.music-name {
    display: inline-block;
    width: 65%;
    vertical-align: top;
    margin-top: 0.75rem;
    margin-left: 0.5rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    cursor: pointer;

    padding-bottom: 1rem;
  }

  span.del-icon {
    display: inline-block;
    position: absolute;
    right: 0.5rem;
    top: 1rem;
    width: 1rem;
    height: 1rem;
    background: url('./del.svg');
    background-size: contain;
    cursor: pointer;
  }

.tips {
  text-align: center;
  margin: 0.6rem auto;
  width: 10rem;
  font-size: 80%;
  color: gray;
}
  
</style>



