<template>
  <div id="app">
     <!-- 主界面部分 -->
    <transition name="show">
      <div v-show="isShowIndex" class="index">
        <!-- 头部 -->
        <VHeader></VHeader>

        <!-- 侧边栏 -->
        <AsideMenu v-show="isShowAsideMenu"></AsideMenu>

        <!-- router控制的Tab页内容 -->
        <router-view></router-view>

        <!-- 尾部mini播放器 -->
        <VFooter></VFooter>
      </div>
    </transition>

    <!-- 播放界面 -->
    <transition name="showIndex">
      <Play v-show="!isShowIndex"></Play>
    </transition>


    <!-- 隐藏的audio标签  control控制是否出现 -->
    <audio v-bind:src="audio.src || (musicData[0]&&musicData[0].src) || defaultSrc" v-bind:autoplay="isPlaying" ref="audio" ></audio>


    <!-- 关于界面 -->
    <About v-if="isShowAbout"></About>
  </div>
</template>

<script>
import VHeader from './components/Header/Header.vue';
import VFooter from './components/Footer/Footer.vue';
import AsideMenu from './components/AsideMenu/AsideMenu.vue';
import About from './components/About/About.vue';
import Play from './components/Play/Play.vue';


import {api} from './global/api.js';
export default {
  name: 'app',
  data() {
    return {
      defaultSrc: 'http://sc1.111ttt.com/2016/1/12/10/205102127335.mp3'
    };
  },
  components: {
    VHeader,
    AsideMenu,
    About,
    VFooter,
    Play
  },
   mounted() {
        this.$store.commit('findDOM', {name: 'audio', dom: this.$refs.audio});//this.$refs.audio获取元素audio
   },

  computed: {
    //获取audio
    audio() {
      return this.$store.state.audio;
    },
    //获取musicData
    musicData() {
      return this.$store.state.musicData;
    },
    isPlaying() {
      return this.$store.state.isPlaying;
    },
    //在计算属性中返回首页展示状态：
    isShowIndex() {
      return this.$store.state.isShowIndex;
    },
    //获取是否展示侧边栏
    isShowAsideMenu() {
      return this.$store.state.isShowAsideMenu;
    },
    //获取是否展示关于界面
    isShowAbout() {
      return this.$store.state.isShowAbout;
    },
   
    isShowSearch() {
      return this.$store.state.isShowSearch;
    },
  },
  methods: {
    next() {
      this.audio.index = this.audio.index === this.musicData.length - 1 ? 0 : (++this.audio.index);
      this.$store.commit('toggleMusic', this.audio.index);
    }
  }

}
</script>

<style>
</style>



