<template >

  <transition name="fade"><!-- vue.js过渡效果 -->
    <div v-show="isShowMiniMusic" :style="{backgroundColor: skinColor}" class="footer">
      <div class="mini-music">

        <div class="music-img">
          <img @click="showPlay" ref="img" v-bind:src="audio.musicImgSrc || (musicData[0]&&musicData[0].musicImgSrc) || defaultImg" >
        </div>

        <div class="music-name">
          <p @click="showPlay">{{audio.name || (musicData[0]&&musicData[0].name) || '出不来啊'}}</p>

          <!-- 进度条 -->
          <div class="progress">
            <span class="start">{{transformTime(now)}}</span><!-- 开始的时间，记录变换的时间 -->

            <div @click="changeTime($event)" @touchmove="touchMove($event)" @touchend="touchEnd($event)" ref="progressBar" class="progress-bar">
              <div class="now" ref="now" :style="{width: (now / nativeAudio.duration).toFixed(3)*100 + '%'}"></div>
            </div>

            <span class="end" v-text="totalTime"></span><!-- 一首歌完整的时间 -->
          </div>

        </div>

        <div class="music-control">
          <i @click="play()" v-bind:class="[isPlaying ? pauseIcon : playIcon]"></i><!-- 根据是否播放显示不同的图标 -->
        </div>

      </div>
    </div>

  </transition>

</template>

<script>
export default {

  //实例挂载完成后执行
  mounted() {
   // console.log(this.DOM.audio);
    this.nativeAudio = document.querySelector('audio');//获取文档中第一个 <audio> 元素：
    this.nativeAudio.addEventListener('play', () => {  //添加播放绑定事件
      this.totalTime = this.transformTime(this.nativeAudio.duration);//.duration获得当前音频的长度
      this.now = this.nativeAudio.currentTime;//currentTime 属性设置或返回音频/视频播放的当前位置（以秒计）,
                                              //当设置该属性时，播放会跳跃到指定的位置
      setInterval(() => {
        this.now = this.nativeAudio.currentTime;
      }, 1000)//setInterval() 方法会每隔一秒不停地调用函数
    })
  },
  computed: {
    isPlaying() {
      return this.$store.state.isPlaying;
    },
    isShowAsideMenu() {
      return this.$store.state.isShowAsideMenu;
    },
    isShowMiniMusic() {
      return this.$store.state.isShowMiniMusic;
    },
    audio() {
      return this.$store.state.audio;
    },
    DOM() {
            return this.$store.state.DOM;
    },
    musicData() {
      return this.$store.state.musicData;
    },
    //获取当前皮肤颜色
    skinColor() {
      return this.$store.state.skinColor;
    }

  },
  data() {
    return {
      playIcon: 'play-icon',
      pauseIcon: 'pause-icon',
      now: 0,
      nativeAudio: {},
      totalTime: '0:00',
      defaultImg: 'https://microzz.com/img/avatar.jpg'
    }
  },
  methods: {
    //真正的音乐播放，暂停
    play() {
      //console.log("wolaila")
      this.$store.commit('play', !this.isPlaying);
       // !this.isPlaying ? console.log("暂停"): console.log("播放");
       !this.isPlaying ? this.DOM.audio.pause() : this.DOM.audio.play();//播放不了
    },
    showPlay() {
      if (this.isShowAsideMenu) {
        return;
      }
      this.$store.commit('showIndex', false);
      this.$store.commit('showMiniMusic', false);
    },
    changeTime(event) {
      let progressBar = this.$refs.progressBar;
      let coordStart = progressBar.getBoundingClientRect().left;
      let coordEnd = event.pageX;
      this.nativeAudio.currentTime = (coordEnd - coordStart) / progressBar.offsetWidth * this.nativeAudio.duration;
      this.now = this.nativeAudio.currentTime;
      this.nativeAudio.play();
      this.$store.commit('play', true);
    },
    touchMove(event) {
      let progressBar = this.$refs.progressBar;
      let coordStart = progressBar.getBoundingClientRect().left;
      let coordEnd = event.touches[0].pageX;
      this.$refs.now.style.width = ((coordEnd - coordStart) / progressBar.offsetWidth).toFixed(3) * 100 + '%';
    },
    touchEnd(event) {
      this.nativeAudio.currentTime = this.$refs.now.style.width.replace('%', '')/100 * this.nativeAudio.duration;
      this.now = this.nativeAudio.currentTime;
      this.nativeAudio.play();
      this.$store.commit('play', true);
    },

    transformTime(seconds) {
      let m, s;
      m = Math.floor(seconds / 60);
      m = m.toString().length == 1 ? ('0' + m) : m;
      s = Math.floor(seconds - 60 * m);
      s = s.toString().length == 1 ? ('0' + s) : s;
      return m + ':' + s;
    }

  }
}
</script>

<style  scoped>
  .fade-enter-active {
    transition: all .3s ease-in-out;
  }
  .fade-leave-active {
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .fade-enter, .fade-leave-active {
    transform: translateY(2.5rem);
    opacity: 0;
  }
  * {
      margin: 0;
      padding: 0;
      box-sizing: border-box;
  }


  .footer {
    position: fixed;
    bottom: 0;
    background: #B72712;
    width: 100%;
    height: 3.5rem;
    text-align: center;
   }

  .mini-music {
    display: flex;/*弹性布局*/
    height: 3.5rem;
    line-height: 3.5rem;
  }
  .music-img {
    width: 3.5rem;
    height: 3.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  img {
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 1.75rem;
    background-size: contain;
    cursor: pointer;
  }
      
  .music-name {
    flex: 5;
    color: white;
  /*  overflow: hidden;*/
    position: relative;
    width: 100%;
  }
  p {
    position: relative;
    z-index: 1;
    height: 2rem;
    line-height: 2rem;
    overflow: hidden;
    white-space: nowrap;
    // cursor: pointer;
  }
  .progress {
    position: absolute;
    width: 100%;
    top: 0.5rem;
    left: 0;
  }
  span.start {
    position: absolute;
    left: 0.3rem;
    
  }
  span.end {
    position: absolute;
    right: 0.2rem;
    // padding-left:0.25rem;
  }
  @media screen and(min-width: 30rem) {
    span.start {
      position: absolute;
      left: 1.3rem;
    }
    span.end {
      position: absolute;
      right: 1.5rem;
    }
  }
  .progress-bar {
    position: relative;
    width: 50%;
    height: 0.25rem;
    display: inline-block;
    background-color: rgba(255, 255, 255, .5);
    vertical-align: 0.1rem;
    border-radius: 0.15rem;
    cursor: pointer;
  }
  .now {
    position: absolute;
    left: 0;
    display: inline-block;
    max-width: 100%;
    /* width: 70%;*/
    height: 0.25rem;
    background-color: #31c27c;
  }
  .now::after {
    content: "";
    position: absolute;
    left: 100%;
    width: 0.1rem;
    height: 0.3rem;
    background-color: white;
  }
          
  .music-control {
    flex: 1.5;
  }
  i {
    padding-right: 0.5rem;
    width: 2.25rem;
    height: 2.25rem;
    margin-top: 0.65rem;
    display: inline-block;
    cursor: pointer;
    border-radius: 1.1rem;
  }
  .play-icon {
    background: url(./play.svg) no-repeat;
    background-size: contain;

  }
  .pause-icon {
    background: url(./pause.svg) no-repeat;
    background-size: contain;
  }
     
</style>



