<template >
  <transition name="fadeIn"><!-- vue.js过渡效果 -->
    <div  class="aside-menu">
      <!-- 收回侧栏的←箭头 -->
      <i class="fa fa-arrow-left fa-2x back" @click="showAsideMenu" ></i>
      
      <div class="aside">
        <div :style="{backgroundColor: skinColor}" class="info">
          <a  class="avatar"><img src="../../../static/photo.jpg" alt=""></a>
          <div class="about">
            <span class="name">huihui</span>
            <span class="level">Lv100</span>
            <span @click="isSignIn=true" class="sign">
              {{isSignIn ? '已签到' : '签到'}}
            </span>
          </div>
        </div>

        <div class="settings">
          <div class="content">
            <ul>
              <li><i class="icon-msg"></i>我的消息</li>
              <li><i class="icon-vip"></i>会员中心</li>
              <li><i class="icon-shop"></i>商城</li>
              <li><i class="icon-music"></i>在线听歌免流量</li>
            </ul>
            <ul>
              <li><i class="icon-friend"></i>我的好友</li>
              <li><i class="icon-near"></i>附近的人</li>
            </ul>
            <ul>
            <!-- 阻止默认行为 -->
              <li @click.stop.prevent="$store.commit('showIndex', false)"><i class="icon-skin"></i>个性换肤</li>
              <li @click="showAbout"><i class="aboutme"></i>关于</li>
            </ul>
            <div class="back">
              <i @click.stop.prevent="showAsideMenu" class="icon-back"></i>
            </div>
          </div>

        </div>
      </div>

      <!-- 点击此区域showAsideMenu变false -->
      <div @click.stop.prevent="showAsideMenu" class="mask"></div>


    </div>
  </transition>

</template>

<script>
export default {
  name: 'AsideMenu',
  computed: {
    skinColor() {
      return this.$store.state.skinColor;
    }
  },
  data() {
    return {
      isSignIn: false
    }
  },
  methods: {
    //点击←则触发此事件，修改showAsideMenu为false
    showAsideMenu() {
      this.$store.commit('showAsideMenu', false);
    },

    //点击关于，展示关于的界面
    showAbout() {
      this.$store.commit('showAbout', true);
    }
  }
}
</script>

<style  scoped>

.fadeIn-enter-active {
  transition: all .4s ease;
}
.fadeIn-leave-active {
  transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.fadeIn-enter, .fadeIn-leave-active {
  transform: translateX(-12.5rem);
  opacity: 0;
}
.aside-menu {
  width: 100%;
  /*height: 100%;*/
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;/*弹性布局*/
  z-index: 1;
}
.back {
     display: inline-block;
     position: absolute;
     left: 50%;
     top: 0.5rem;
     width: 1rem;
     height: 1rem;
     background-size: contain;
     color: white;
}
.aside {
  flex: 6;
}
.mask {
  flex: 4;
}
.info {
  padding-top: 1rem;
  padding-left: 0.5rem;
  padding-bottom: 0.4rem;
  background-color: #B72712;
  box-shadow: 0.1rem 0 0.5rem gray;/*阴影*/
  flex: 1;
}    
a{
  text-decoration: none;
}
a.avatar:hover::after {
  transform: scaleX(0);
}
a::after {
  background-color: white;
}
img {
  width: 5rem;
  height: 4.75rem;
  border-radius: 3rem;
}
.about {
  position: relative;
  margin-top: 0.1rem;
}
span.name {
  color: rgba(255, 255, 255, .8);
  font-size: 1.2rem;

}
span.level {
  width: 0.75rem;
  height: 0.75rem;
  border: 0.05rem solid white;
  border-radius: 1rem;
  padding: 0 0.3rem;
  color: white;
  font-size: .2rem;
  font-style: italic;
}
span.sign {
  position: absolute;
  border: 0.05rem solid white;
  text-align: center;
  border-radius: 0.5rem;
   right: 1rem;
  color: white;
  height: 1rem;
  line-height: 1rem;
  top: 0.25rem;
  font-size: .8rem;
  width: 2.5rem;
  cursor: pointer;
}

.settings {
  flex: 3.2;
  padding-top: 0.05rem;
  position: relative;
  height: 100%;
  background: white;
  border-right: 0.05rem solid gray;
  box-shadow: 0.1rem 0 1rem gray;
}
.content {
  height: 100%;
}

ul:nth-of-type(1) {
  color: rgba(0, 0, 0, .5);
 /* padding-top: 20px;*/
  border-bottom: 0.3rem solid rgba(0, 0, 0, .04);
}
ul:nth-of-type(2) {
  color: rgba(0, 0, 0, .5);
  padding-top: 0.5rem;
  padding-bottom: 0.25rem;
  border-bottom: 0.3rem solid rgba(0, 0, 0, .04);
}
ul:nth-of-type(3) {
  padding-top: 0.75rem;
}
ul {
  list-style: none;
  padding: 0.25rem 0.75rem;
}
li {
  font-size: 1rem;
  margin-bottom: 1rem;
  cursor: pointer;
}
i {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  margin-right: 0.2rem;
  vertical-align: text-bottom;/*把元素的底端与父元素字体的底端对齐。*/
}
i.icon-skin {
  background: url('./skin.svg') no-repeat;
  background-size: contain;

}
i.icon-msg {
  background: url('./msg.svg') no-repeat;
  background-size: contain;
}
i.icon-vip {
  background: url('./vip.svg') no-repeat;
  background-size: contain;
}
i.icon-shop {
  background: url('./shop.svg') no-repeat;
  background-size: contain;
}
i.icon-music {
  background: url('./music.svg') no-repeat;
  background-size: contain;
}
i.icon-friend {
  background: url('./friend.svg') no-repeat;
  background-size: contain;
}
i.icon-near {
  background: url('./near.svg') no-repeat;
  background-size: contain;
}

i.aboutme {
  display: inline-block;
  margin-right: 0.1rem;
  width: 1rem;
  height: 1rem;
  background: url('./about.svg') no-repeat;
  background-size: contain;/*把图像图像扩展至最大尺寸，以使其宽度和高度完全适应内容区域。*/
}
    
</style>



