<template lang="html">
  <div :style="{backgroundColor: skinColor}" class="header">
  
    <div class="name">
      <span class="func"><i class="fa fa-bars fa-lg left" @click="showAsideMenu(true)"></i></span>
      <span class="text">云音乐</span>
      <span class="search"><i class="fa fa-search fa-lg right" @click="toSearch" ></i></span>
    </div>

    <div class="tab">
      <div v-bind:class="{link: linkBorderIndex==1}" class="item"><!-- v-bind实现动态绑定link-未实现 -->
        <router-link to="/music-list">我的</router-link>
      </div>
      <div v-bind:class="{link: linkBorderIndex==2}" class="item">
        <router-link to="/find">发现</router-link>
      </div>
      <div v-bind:class="{link: linkBorderIndex==3}" class="item">
        <router-link to="/social">一个</router-link>
      </div>
    </div>

  </div>
</template>

<script>

export default {
  name: 'header',

  data() {
    return {
      index: ''
    };
  },
  computed: {
    linkBorderIndex() {
      return this.$store.state.linkBorderIndex;
    },
    skinColor() {
      return this.$store.state.skinColor;
    }
  },
  methods: {
    toSearch() {
      this.$router.push('/find');
    },
    showAsideMenu(flag) {
      this.$store.commit('showAsideMenu', flag);
    }
  }
}
</script>

<style  scoped>
  .header{
    position: fixed;
    width: 100%;
    z-index: 1;
  }
  .header .name{
    padding-top: 0.5rem;
    padding-bottom: 1.5rem;
    border-bottom: 0.05rem solid rgba(255, 255, 255, .2);
  }
  .name span{
    width: 32.8%;
    color: white;
    float: left;
  }
  span.text{
    text-align: center;
  }
  .left{
    float: left;
    padding-left: 0.25rem;
    cursor: pointer;
  }
  .right{
    line-height: 1rem;
    float: right;
    padding-right: 0.25rem;
    cursor: pointer;
  }
  .tab {
    display: flex;
    border-bottom: 0.05rem solid #9E9E9E;
  }
  a{
    display: inline-block;
    width: 50%;
    color: white;
    text-decoration: none;
  }
  /* 横条*/
  a::after {
    background-color: rgba(255, 255, 255, .2);
    height: 0.05rem;
    bottom: -0.1rem;
  }
  .item {
    display: inline-block;
    flex: 1;
    text-align: center;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
  }
  .link {
    font-weight: bold;
    color: #BDBDBD;
    border-bottom: 0.05rem solid #E0F2F1;
  }
  
</style>



