// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'

import axios from 'axios';
import VueAxios from 'vue-axios';
import Vuex from 'vuex'
import './rem.js'
Vue.config.productionTip = false

// 用 axios 进行 Ajax 请求
Vue.use(VueAxios, axios);

// Vuex 进行状态管理
Vue.use(Vuex);

import {api} from './global/api.js';
const store = new Vuex.Store({
	state:{
		aPlay:'',
		musicData: [
			{
		       "name": "Isyana Sarasvati - 跟随你",
		       "src": " http://ws.stream.qqmusic.qq.com/9072044.m4a?fromtag=46",
		       "musicImgSrc": "https://img3.doubanio.com/lpic/s27781905.jpg"
		     },
		     {
		       "name": "苏打绿 - 我好想你",
		       "src":"http://ws.stream.qqmusic.qq.com/4884947.m4a?fromtag=46",
		       "musicImgSrc": "http://imgcache.qq.com/music/photo/album_300/7/300_albumpic_430607_0.jpg"
		     },
		     {
		       "name": "Wanna One - Energetic",
		       "src": "http://ws.stream.qqmusic.qq.com/203512189.m4a?fromtag=46",
		       "musicImgSrc": "http://imgcache.qq.com/music/photo/album_300/76/300_albumpic_2195876_0.jpg"
		     },
		     {
		       "name": "KARD - Hola Hola",
		       "src":"http://ws.stream.qqmusic.qq.com/203268226.m4a?fromtag=46",
		       "musicImgSrc": "http://imgcache.qq.com/music/photo/album_300/17/300_albumpic_2165417_0.jpg"
		     },
		     {
		       "name": "KARD - Don't Recall",
		       "src":"http://ws.stream.qqmusic.qq.com/200711957.m4a?fromtag=46",
		       "musicImgSrc": "http://imgcache.qq.com/music/photo/album_300/19/300_albumpic_1850119_0.jpg"
		     },
		     {
		       "name": "BLACKPINK - As If It’s Your Last",
		       "src": "http://ws.stream.qqmusic.qq.com/202831830.m4a?fromtag=46",
		       "musicImgSrc": "http://imgcache.qq.com/music/photo/album_300/43/300_albumpic_2118543_0.jpg"
		     },
		     {
		       "name": "BLACKPINK - PLAYING WITH FIRE",
		       "src": "http://ws.stream.qqmusic.qq.com/109007971.m4a?fromtag=46",
		       "musicImgSrc": "http://imgcache.qq.com/music/photo/album_300/88/300_albumpic_1663588_0.jpg"
		     }
		],
		isShowIndex: true,
		skinColor: localStorage.skinColor || '#B72712',
		linkBorderIndex: '',
		isShowAsideMenu: false,//默认隐藏起来的
		isShowAbout: false,//关于的页面
		isPlaying: false,
		isAnimation: false,
		isShowMiniMusic: false,
		DOM:{},
		audio: {//音频
		  name: '',
		  src: '',
		  musicImgSrc: '',
		  index: 0,
		}
	},
	mutations: {
		//创建audio对象
		// newAudio(state,flag){
		//   state.aPlay = new Audio()
		// },
		//侧栏
		findDOM(state, payload) {//payload是个对象
		  state.DOM[payload.name] = payload.dom;
		},
		showAsideMenu(state, flag) {
		  state.isShowAsideMenu = flag;
		},
		//关于
		showAbout(state, flag) {
		  state.isShowAbout = flag;
		},
		//迷你播放器
		showMiniMusic(state, flag) {
		  state.isShowMiniMusic = flag;
		},
		showIndex(state, flag) {
		  state.isShowIndex = flag;
		},
		//换肤
		changeSkinColor(state, color) {
		  state.skinColor = color;
		},
		//删除操作
		del(state, index) {
			console.log("  来到mutation，进行真正数据删除");
		  if (state.musicData.length === 0) {
		    return;
		  }
		  state.musicData.splice(index, 1);//删除当前下标开始的数据，删除一个
		},
		//切换音乐
		toggleMusic(state, index) {
		    state.audio.name = state.musicData[index].name;
		    state.audio.src = state.musicData[index].src;
		    state.audio.musicImgSrc = state.musicData[index].musicImgSrc;
		    state.audio.index = index;
		},
		playMusic(state, payload) {
		  state.audio.name = payload.name;
		  state.audio.src = payload.src;
		  state.audio.musicImgSrc = payload.imgSrc;
		  state.isPlaying = true;
		},
		play(state, flag) {
		  //console.log("来到mutations")
		  state.isPlaying = flag;
		  // if(state.isPlaying){
			 //  state.aPlay.src=state.audio.src||(state.musicData[0]&&state.musicData[0].src);
			 //  state.aPlay.play();
		  // }else{
				// state.aPlay.pause();
		  // }
		},
		changeLinkBorderIndex(state, index) {
		   state.linkBorderIndex = index;
		},
		
	},
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



